___CSS_LOADER_EXPORT___.push([module.id, `/* Trail legend and controls styles */

.trail-legend {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 10;
  background-color: rgba(30, 30, 30, 0.9);
  border: 1px solid #444;
  border-radius: 8px;
  padding: 0.75rem;
  min-width: 180px;
  backdrop-filter: blur(4px);
}

.trail-controls {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.trail-toggle {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
}

.trail-toggle input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
  accent-color: #3b82f6;
}

.color-mode-selector {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.color-mode-selector label {
  font-size: 0.8rem;
  color: #999;
}

.mode-buttons {
  display: flex;
  gap: 0.25rem;
}

.mode-btn {
  flex: 1;
  padding: 0.4rem 0.5rem;
  background-color: #2a2a2a;
  border: 1px solid #444;
  border-radius: 4px;
  color: #ccc;
  font-size: 0.75rem;
  cursor: pointer;
  transition: all 0.15s ease;
}

.mode-btn:hover {
  background-color: #333;
  border-color: #555;
}

.mode-btn.active {
  background-color: #3b82f6;
  border-color: #60a5fa;
  color: #fff;
}

.trail-length-control {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.trail-length-control label {
  font-size: 0.8rem;
  color: #999;
}

.trail-length-control input[type="range"] {
  width: 100%;
  height: 4px;
  -webkit-appearance: none;
  appearance: none;
  background: #444;
  border-radius: 2px;
  cursor: pointer;
}

.trail-length-control input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 14px;
  height: 14px;
  background: #3b82f6;
  border-radius: 50%;
  cursor: pointer;
}

.legend-display {
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid #444;
}

.legend-title {
  font-size: 0.8rem;
  color: #999;
  margin-bottom: 0.4rem;
}

.legend-bar {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.gradient-bar {
  height: 12px;
  border-radius: 3px;
  border: 1px solid #555;
}

.legend-labels {
  display: flex;
  justify-content: space-between;
}

.legend-label {
  font-size: 0.7rem;
  color: #888;
}
